


import React, { Component } from 'react';
import {Connect} from '../../../lib/index';

@Connect((state)=>{
    return {
        data:state.counter
    }
})
class CounterContainer extends Component {
    render() {
        const data = this.props.data;
        return (
            <div>
                {data.value}
            </div>
        );
    }
}

export default CounterContainer;